.container-detail {
    background-image: url("../../img/details2.jpg");
    background-position: center center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;  
}

.details-div {
    display: flex;
    justify-content: center;
    border: 1px solid black;
    z-index: 1;
    margin: 10px;
    background-color: rgb(149, 187, 223);
    opacity: 0.8;
}

.details-div p {
    display: flex;
    justify-content: center;
    color: black;
    font-size: larger;
    font-weight: 500;
}

.div-img {
    display: flex;
    justify-content: center;
}

.details-div img{
    border: 5px solid black;
    
    display: flex;
    justify-content: center;
    text-align: center;
    margin: 10px;
    width: 600px;
    height: 400px;
}

.title {
    display: flex;
    justify-content: center;
    font-size:xx-large;
    color: black;
}

.div-descr {
    margin: 20px;
}

.div-descr p {
    display: flex;
    justify-content: center;
}

.descripcion {
    text-align: justify;
}

.div-descr button {
    margin: 5px;
    width: 100px;
    height: 40px;
    font-family: 'Press Start 2P';
    position: relative;
    left: 46%;
   
    background-color:gold
}

.div-descr button:hover {
    background-color: black;
    color: aqua;
}